import React from 'react';
import { Link } from 'gatsby';
//import AniLink from "gatsby-plugin-transition-link/AniLink"

const NavLink = props => (
    <Link
      {...props}
      getProps={({ isCurrent }) => {
        return {
        className: isCurrent ? "nav-link regular" : "nav-link"
        };
      }}
    />
  );

export default function NavElement(props){
    return(
        <li><NavLink to={'/'+props.url} className="nav-link" onClick={props.onClickEvent}>{props.children}</NavLink></li>
    );
}